const app = getApp()
export default {
	data() {
		return {
			frontCamera: true,
			cameraContext: {},
			pushUrl: "",
			mode: "SD",
			muted: false,
			enableCamera: true,
			orientation: "vertical",
			beauty: 6.3,
			whiteness: 3.0,
			backgroundMute: false,
			debug: false,
			headerHeight: app.globalData.headerHeight,
			statusBarHeight: app.globalData.statusBarHeight,
		}
	},
	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		console.log("onLoad");
		this.mode=options.mode
		this.orientation= options.orientation
		this.enableCamera= options.enableCamera === "false" ? false : true
		this.pushUrl= decodeURIComponent(options.pushUrl)
	},
	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {
		console.log("onLoad onReady");
		this.createContext();

		uni.setKeepScreenOn({
			keepScreenOn: true,
		})
	},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		console.log("onLoad onShow");
		// 保持屏幕常亮
		wx.setKeepScreenOn({
			keepScreenOn: true
		})
	},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {
		console.log("onLoad onHide");

	},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {
		console.log("onLoad onUnload");
		this.stop();

		wx.setKeepScreenOn({
			keepScreenOn: false,
		})
	},


	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {
		console.log("onLoad onShareAppMessage");
		return {
			// title: 'RTMP推流',
			// path: '/pages/push/push',
			path: '/pages/home-page/main',
			imageUrl: 'https://mc.qcloudimg.com/static/img/dacf9205fe088ec2fef6f0b781c92510/share.png'
		}
	},

	methods: {
		onBack: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		onSwitchCameraClick: function() {
			this.frontCamera = !this.frontCamera;
			this.frontCamera=this.frontCamera
			this.cameraContext.switchCamera();
		},
		onBeautyClick: function() {
			if (this.beauty != 0) {
				this.beauty = 0;
				this.whiteness = 0;
			} else {
				this.beauty = 6.3;
				this.whiteness = 3.0;
			}

				this.beauty= this.beauty,
				whiteness= this.whiteness
		},
		onLogClick: function() {
				this.debug=!this.debug
		},
		onMuteClick: function() {
				this.muted= !this.muted
		},
		onPushEvent: function(e) {
			uni.showModal({
				content: e.detail.code,
				showCancel: false
			});
			console.log(e.detail.code);
			if (e.detail.code == -1307) {
				this.stop();
				wx.showToast({
					title: '推流多次失败',
				})
			}
		},

		stop: function() {
				this.playing= false,
				this.pushUrl= "",
				this.mode= "SD",
				this.muted= false,
				this.enableCamera= true,
				this.orientation= "vertical",
				this.beauty= 6.3,
				this.whiteness= 3.0,
				this.backgroundMute= false,
				this.debug= false
			this.cameraContext.stop();
		},

		createContext: function() {
			// var self = this;
				this.cameraContext=uni.createLivePusherContext('camera-push'),
				this.cameraContext.start();
		},

	}
}
